<template>
  <div>
    <el-table
      :data="member"
      style="width: 100%; margin-bottom: 20px"
      row-key="id"
      border
      :tree-props="{ children: 'children', hasChildren: 'hasChildren' }"
    >
      <el-table-column prop="uid" label="用户编号" sortable width="180">
      </el-table-column>
      <el-table-column prop="nickname" label="昵称" sortable width="180">
      </el-table-column>
      <el-table-column prop="phone" label="手机号">
      </el-table-column>
      <el-table-column prop="status" label="状态">
        <template slot-scope="scope">
          <el-button type="primary" v-if="scope.row.status == 1"
            >启用</el-button
          >
          <el-button type="primary" v-if="scope.row.status == 2"
            >禁用</el-button
          >
        </template>
      </el-table-column>
      <el-table-column prop="icon" label="操作">
        <template slot-scope="scope">
          <el-button type="primary" @click="edit(scope.row.uid)">修改</el-button>
          <!-- <del-btn @del="delbutton(scope.row.id)"></del-btn> -->
        </template>
      </el-table-column>
    </el-table>
  </div>
</template>
<script>
import { mapGetters, mapActions } from "vuex";
import { menuDel } from "../../../utils/request";
import { alertWarn, alertSuccess } from "../../../utils/alert";
export default {
  props: [],
  components: {},
  data() {
    return {};
  },
  computed: {
    ...mapGetters({
      member: "member",
    }),
  },
  methods: {
    ...mapActions({
      getmemberList: "getmemberList",
    }),
    edit(uid) {
      this.$emit("update", uid);
    },
    // del(id){
    // this.$confirm('是否确认删除?', '提示', {
    //         confirmButtonText: '确定',
    //         cancelButtonText: '取消',
    //         type: 'warning'
    //       }).then(() => {
    //           menuDel(id).then(res=>{
    //             if(res.data.code==200){
    //               alertSuccess(res.data.msg)
    //               this.getMenuList()
    //             }else{
    //               alertWarn(res.data.msg)
    //             }
    //           })
    //       }).catch(() => {
    //         this.$message({
    //           type: 'info',
    //           message: '已取消删除'
    //         });
    //       });

    // }
    delbutton(id) {
      menuDel(id).then((res) => {
        if (res.data.code == 200) {
          alertSuccess(res.data.msg);
          this.getMenuList();
        } else {
          alertWarn(res.data.msg);
        }
      });
    },
  },
  mounted() {
    this.getmemberList();
  },
};
</script>
<style scoped>
</style>


