<template>
<div>
    <v-list @update="update"></v-list>
    <v-add ref="vchild" :update="isadd"></v-add>
</div>
</template>
<script>
 import {mapGetters,mapActions} from 'vuex';
 import vList from './component/list'
 import vAdd from './component/add'
export default {
props: [],
components: {vList,vAdd},
data() {
return {
    isadd:false
};
},
computed: {
...mapGetters({}),
},
methods: {
...mapActions({}),
// isShow(){
//     this.isadd=true
//     this.$refs.vchild.dialogFormVisible = true
// },
update(uid){
    this.isadd=false
    this.$refs.vchild.dialogFormVisible = true
    this.$refs.vchild.getmemberDetail(uid)
}
},
mounted() {},
};
</script>
<style scoped>
.btn{
    margin-top: 20px;
}
</style>


