<template>
  <div>
    <el-dialog title="菜单修改" :visible.sync="dialogFormVisible">
      
      <el-form :model="form">
        <el-form-item label="手机号" :label-width="formLabelWidth">
          <el-input v-model="form.phone" autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item label="昵称" :label-width="formLabelWidth">
          <el-input v-model="form.nickname" autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item label="密码" :label-width="formLabelWidth">
          <el-input v-model="form.password" autocomplete="off"></el-input>
        </el-form-item>
        
        
        

        <el-form-item label="状态">
            
          <el-switch
            v-model="form.status"
            :active-value="1"
            :inactive-value="2"
            active-color="#13ce66"
            inactive-color="#ff4949">
          </el-switch>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="cancle">取 消</el-button>
        <!-- <el-button type="primary" @click="addMenuList" v-if="update">添加</el-button> -->
        <el-button type="primary" @click="updatemember">修改</el-button>
      </div>
    </el-dialog>
  </div>
</template>
<script>
import { mapGetters, mapActions } from "vuex";
import {alertSuccess,alertWarn} from '../../../utils/alert'
import { memberedit,memberinfo} from "../../../utils/request";
export default {
  props: ['update'],
  components: {},
  data() {
    return {
      dialogFormVisible: false,
      form: {
        uid: 0,
        nicknanme: "",
        phone: "",
        password:1,
        status: 1,
      },
      formLabelWidth: "120px",
    };
  },
  computed: {
    ...mapGetters({}),
  },
  methods: {
    ...mapActions({
      getmemberList:"getmemberList"
    }),
    cancle(){
        this.dialogFormVisible=false;
        this.clearForm()
    },
    clearForm(){
      this.form={
        uid: 0,
        nicknanme: "",
        phone: "",
        password:1,
        status: 1,
      }
    },
    getmemberDetail(uid){
      memberinfo(uid).then(res=>{
        this.form=res.data.list
        this.form.uid=uid
        this.form.password=''
      })
    },
    updatemember(){
      memberedit(this.form).then(res=>{
        if(res.data.code==200){
                alertSuccess(res.data.msg)
                this.dialogFormVisible = false;
                this.clearForm()
                this.getmemberList()
              }else{
                alertWarn(res.data.msg)
              }
      })
    },
    
  },
  mounted() {
      
  },
};
</script>
<style scoped>
</style>


